form {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin: auto;
  max-width: 500px;
}

label {
  color: #eee;
}
form label {
  margin-bottom: 0.5rem;
}

form input,
textarea {
  margin-bottom: 1rem;
  padding: 8px 15px;
  font-size: 1.2rem;
  font-family: "Poppins", sans-serif;
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.1);
  color: #f4f4f4;
}
